<template>
  <AppHeader />
  <div class="w-full flex">
    <div class="m-auto">
      <h1 class="text-center mb-6 text-3xl text-">Number of To Do's {{ num }}</h1>
      <div id="ulLists">
      <ul id="append" class="justify-between"> {{ get }}
        <li id="listItem" class="flex justify-between mb-1" v-for="(num, index) in names" v-bind:key="index">
          {{ index }}. {{ num }}
          <div>
            <button class="hover:bg-red-600 pl-3 pr-3 rounded-lg" @click="Done(index)">/</button>
            <button class="hover:bg-red-600 pl-3 pr-3 rounded-lg" @click="Delete(index)">X</button>
          </div>
        </li>
      </ul>
      </div>
      
      <div class="mt-5">
        <input class="bg-slate-500 rounded-xl pl-2 text-pink-50" id="in" type="text" v-model="newName">
        <button class="bg-gradient-to-t from-slate-500 to-teal-500 ml-4 rounded-l rounded-r p-1 hover:bg-teal-500" @click="Create" type="submit">submit</button>
      </div>
    </div>
  </div>
</template>

<script>
import AppHeader from './components/AppHeader.vue';
// import $ from 'jquery';

// localStorage.clear();

export default {
  components: {
    AppHeader
  },
  data(){
    return {
      newName: "",
      counter: 0,
      names : [],
    }
  },
  created () {
    if (localStorage.getItem("Storage_key") !== null){
      this.names = JSON.parse(localStorage.getItem("Storage_key"));
    }
    else {
      this.names = [];
    }
  },
  computed: {
    num(){
      return this.names.length;
    },
  },
  methods: {
    randomM(){
      return Math.random();
    },
    Create(){
      if (this.newName == ""){
        alert("Please enter a name");
        return;
      }
      this.names.push(this.newName);
      this.newName = "";
      setTimeout(() => {
        localStorage.setItem("Storage_key", JSON.stringify(this.names)) ;
      }, 500);
    },
    Delete(index){
      this.names.splice(index, 1);
      localStorage.setItem("Storage_key", JSON.stringify(this.names)) ;
    },
    Done (index) {
      console.log( this.names[index] );
    }
  }
}

</script>

